import { Component, OnInit, Output, ViewChild, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { AuthService } from 'src/app/services/auth.service';
declare var $:any;
@Component({
  selector: 'ibss-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  showLogin = true;
  @Output('toggleSection') toggleSection = new EventEmitter();
  login:any = {
    email: "",
    password: "",
    showPassword:false
  }
  registeredEmail:string = "";
  canEnterOTP:boolean = false;
  canChangePassword:boolean = false;
  password:any = {
    new: "",
    confirm: "",
  }
  otp: string = "";
  showOtpComponent:boolean = true;
  @ViewChild('ngOtpInput', { read: false }) ngOtpInput: any;
  otpInputConfig:any = {
    allowNumbersOnly: false,
    length: 6,
    isPasswordInput: false,
    disableAutoFocus: false,
    placeholder: '',
    inputStyles: {
      'width': '50px',
      'height': '50px'
    }
  };
  config: typeof CONFIG = CONFIG;
  pwdResetForm:any = new FormGroup({
    email: new FormControl("", [Validators.required, this.checkEmail()]),
  });
  resetInProcess:boolean = false;
  constructor(
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public authService: AuthService,
    public toastr: ToastrService
  ) {
    if (this.config.isLoggedin) {
      this.router.navigate(['/main']);
    }
   }

  ngOnInit(): void {

  }

  // onLogin(){
  //   this.router.navigate(['/dashboard']);
  // }

  onForgotPwd() {
    this.showLogin = false;
  }

  goBack() {
    this.showLogin = true;
  }
  onGetOPT() {
    this.resetInProcess = true;
    this.registeredEmail = this.pwdResetForm.controls['email'].value;
    const payload = {
      "serviceData": {
        "otpType": 1,
        "requestType": 2,
        "sendTo": this.registeredEmail 
      }
    }
    this.authService.generateOTP(payload)
      .subscribe((res: any) => {
        console.log("generateOTP", res);

        if (res.error && res.error.responseCode === 0) {
          this.canEnterOTP = true;
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }

            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("Login Failed, Please try again after sometimes");
          }
        }
      })
  }

  onOtpChange(otp:any) {
    this.otp = otp;
    if (this.otp.length == this.otpInputConfig.length) {
      const payload = {
        "serviceData": {
          "otpType": 1,
          "otp": Number(otp),
          "sendTo": this.registeredEmail 
        }
      }
      this.config.toggleLoader(true);
      this.authService.verfiyOTP(payload)
        .subscribe((res: any) => {
          console.log("verfiyOTP", res);
          
          if (res.error && res.error.responseCode === 0) {
            this.canChangePassword = true;
          } else {
            if (res.error) {
              if (res.error.businessValidation.length > 0) {
                for (let i = 0; i < res.error.businessValidation.length; i++) {
                  this.toastr.error(res.error.businessValidation[i].errorDesc);
                }
              }
              if (res.error.dataValidation.length > 0) {
                for (let i = 0; i < res.error.dataValidation.length; i++) {
                  this.toastr.error(res.error.dataValidation[i].errorDesc);
                }
              }
            } else if (res.responseCode == -1) {
              this.toastr.error("!!!Oops there seems to be an issue, Please try again after sometimes");
            }
          }
          this.config.toggleLoader(false);
        });
    }
  }

  setVal(val:any) {
    this.ngOtpInput.setValue(val);
  }

  onChangePassword() {

    if (this.password.new && this.password.new.trim().length > 0 && this.password.confirm && this.password.confirm.trim().length > 0 ) {
      if (this.password.new === this.password.confirm) {

        const payload = {
          "serviceData": {
            "otp": this.otp,
            "password": this.password.new ,
            "userId": this.registeredEmail 
          }
        }
        this.config.toggleLoader(true);
        this.authService.forgotPassword(payload)
          .subscribe((res: any) => {
            console.log("forgotPassword", res);
    
            if (res.error && res.error.responseCode === 0) {
              this.toastr.success("Password reset successfull!!. Please login to proceed");
              this.otp = "";
              this.registeredEmail = "";
              this.password = {
                new: "",
                confirm: "",
              };
              this.showLogin = true;
            } else {
              if (res.error) {
                if (res.error.businessValidation.length > 0) {
                  for (let i = 0; i < res.error.businessValidation.length; i++) {
                    this.toastr.error(res.error.businessValidation[i].errorDesc);
                  }
                }
    
                if (res.error.dataValidation.length > 0) {
                  for (let i = 0; i < res.error.dataValidation.length; i++) {
                    this.toastr.error(res.error.dataValidation[i].errorDesc);
                  }
                }
              } else if (res.responseCode == -1) {
                this.toastr.error("Password reset Failed, Please try again after sometimes");
              }
            }
            this.config.toggleLoader(false);
          });
      } else { 
        this.toastr.error("Confirm password doesn't match.");
      }
    } else {
      this.toastr.error("Please enter Password and Confirm Password to procced.");
    }
  }

  onLogin() {
    console.log(this.activatedRoute);
    if (this.login.email && this.login.password ) {
      const username = this.login.email;//"abc@gmail.com";
      const password = this.login.password;//"Thameem@123";
      this.authService.getToken(username, password);
    } else {
      this.toastr.error("Please provide valid email and password to proceed");
    }
  }

  onSignup() {
    // this.router.navigate(['/signup']);
    this.toggleSection.emit('signup');
  }

  checkEmail(): (AbstractContro:any) => ValidationErrors | null {
    return (control: AbstractControl): ValidationErrors | null => {
      return !!control.value &&
        this.config.regEx.email.test(control.value.toLowerCase())
        ? null
        : { isMatching: false };
    };
  }

}
