import { Component, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators, ValidationErrors, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
import { CountryISO, PhoneNumberFormat, SearchCountryField } from 'ngx-intl-tel-input';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'ibss-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {

  @Output('toggleSection') toggleSection = new EventEmitter();

  config: typeof CONFIG = CONFIG;
  currentStep = 1;
  totalStep = 1;
  stepPercent = "25%";
  @ViewChild('sf') sf:any;
  separateDialCode = true;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [CountryISO.India];
  PhoneNumberFormat = PhoneNumberFormat;
  SearchCountryField = SearchCountryField;
  signupForm = new FormGroup({
    firstName: new FormControl("", [Validators.required, Validators.max(75)]),
    middleName: new FormControl("", [Validators.max(75)]),
    lastName: new FormControl("", [Validators.required, Validators.max(75)]),
    mobile: new FormControl("", [Validators.required]), //Validators.pattern("^\\+[1-9]{1,6}\-[d0-9]{10,14}$")
    email: new FormControl("", [Validators.required, this.checkEmail()]),
    password: new FormControl("", [Validators.required, this.checkPassword()]), // Validators.pattern("^(?=.*[A-Z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,20}$")
    confirmPassword: new FormControl("", [
      Validators.required,
      this.matchValues('password'),
    ]),
    agreeTerms: new FormControl(false, [Validators.requiredTrue]),
  });

  isVisible = {
    password: false,
    confirmPassword: false,
  }
  constructor(
    public router: Router,
    public authService: AuthService,
    public toastr: ToastrService
  ) {
  }

  ngOnInit() {
    this.signupForm.controls.password.valueChanges.subscribe(() => {
      this.signupForm.controls.confirmPassword.updateValueAndValidity();
    });
  }

  onLogin() {
    this.toggleSection.emit('login');
  }

  gotToStep(action:any) {

    if (action === 'prev') {
      this.currentStep--;
    } else {
      this.currentStep++;
    }

    this.stepPercent = ((this.currentStep / this.totalStep) * 100) + "%";
  }

  onSignup() {
    //this.router.navigate(['/login']);
    let d = this.signupForm.controls;

    let isValid = true;
    for (let x in d) {
      if (!d[x].valid) {
        isValid = false;
      }
    }

    if (isValid) {
      this.config.toggleLoader(true);
      var mobile = this.signupForm.controls['mobile'].value;
      var signupData = {
        "serviceData": {
          "address1": "",
          "address2": "",
          "branchCode": "",
          "categoryCode": "WEBSITE",
          "city": "",
          "country": "",
          "groupCode": "WEBASIC",
          "idNo": "",
          "idType": "",
          "phone": "",
          "pin": "",
          "roleType": "PA",
          "state": "",
          "status": 0,
          "systemId": "",
          "userId": "",
          "emailID": this.signupForm.controls['email'].value,
          "loginMaster": [
            {
              "loginId": this.signupForm.controls['email'].value,
            }
          ],
          "firstName": this.signupForm.controls['firstName'].value,
          "middleName": this.signupForm.controls['middleName'].value,
          "lastName": this.signupForm.controls['lastName'].value,
          "mobile": mobile.e164Number.replace( mobile.dialCode, mobile.dialCode+"-"),
          "pinMaster": {
            "password": this.signupForm.controls['password'].value,
          }
        }
      }
      console.log('signupData', signupData);
      console.log('signupForm', d, this.sf);
      console.log('signupForm', d, this.sf.valid, this.sf.form.valid);

      this.authService.signup(signupData)
        .subscribe((res: any) => {
          console.log('signup success', res);
          this.config.toggleLoader(false);
          if (res.error && res.error.responseCode == 0) {
            this.authService.getToken(this.signupForm.controls['email'].value, this.signupForm.controls['password'].value, true);
            this.resetForm();
          } else {
            if (res.error) {
              if (res.error.businessValidation.length > 0) {
                for (let i = 0; i < res.error.businessValidation.length; i++) {
                  this.toastr.error(res.error.businessValidation[i].errorDesc);
                }
              }

              if (res.error.dataValidation.length > 0) {
                for (let i = 0; i < res.error.dataValidation.length; i++) {
                  this.toastr.error(res.error.dataValidation[i].errorDesc);
                }
              }
            } else if (res.responseCode == -1) {
              this.toastr.error("Sigup Failed, Please try again after sometimes");
            }

          }
        }, (error) => {
          console.log('signup failure', error);
          this.toastr.error("Sigup Failed, Please try again after sometimes");
          this.config.toggleLoader(false);
        })
      // this.router.navigate(['/dashboard']);
    }

  }

  resetForm() {
    this.signupForm.reset();
    this.isVisible = {
      password: false,
      confirmPassword: false,
    }
  }

  matchValues(
    matchTo: string // name of the control to match to
  ): (AbstractControl:any) => ValidationErrors | null {
    return (control: any): ValidationErrors | null => {
      return !!control.parent &&
        !!control.parent.value &&
        control.value === control.parent.controls[matchTo].value
        ? null
        : { isMatching: false };
    };
  }

  checkEmail(): (AbstractControl:any) => ValidationErrors | null {
    return (control: AbstractControl): ValidationErrors | null => {
      return !!control.value &&
        this.config.regEx.email.test(control.value.toLowerCase())
        ? null
        : { isMatching: false };
    };
  }

  checkPassword(): (AbstractControl:any) => ValidationErrors | null {
    return (control: AbstractControl): ValidationErrors | null => {
      return !!control.value &&
        this.config.regEx.password.test(control.value)
        ? null
        : { isMatching: false };
    };
  }

}
